body {
  margin: 0;
  font-family: "Segoe UI", Arial, sans-serif;
  background: #f4f6f7;
  color: #333;
}

.navbar {
  background: #37474f;
  color: white;
  padding: 15px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-weight: bold;
  font-size: 24px;
}

.nav-links a {
  color: white;
  margin-left: 30px;
  text-decoration: none;
  font-size: 14px;
}

.researcher-section {
  background: #f8f9fa;
  padding: 60px 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
}

.researcher-card {
  text-align: center;
}

.avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #ddd;
  box-shadow: 0 2px 6px rgba(0,0,0,0.2);
  margin-bottom: 10px;
}

.researcher-name {
  font-size: 14px;
}

.footer {
  background: #37474f;
  color: white;
  text-align: center;
  padding: 40px 20px;
}

.footer-links {
  margin-bottom: 20px;
}

.footer-links a {
  color: white;
  margin: 0 12px;
  font-size: 13px;
  text-decoration: none;
}

.footer-text {
  margin: 15px 0;
}

.socials span {
  margin: 0 12px;
  font-size: 20px;
}

.zoho-logo {
  margin-top: 20px;
  font-weight: bold;
  letter-spacing: 6px;
  font-size: 20px;
  color: white;
}

.content-section {
  padding: 40px 400px;
  max-width: 900px;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.content-section h2, .content-section h3 {
  color: #333;
  margin-bottom: 15px;
}

.content-section p, .content-section ul, .content-section table {
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 1.6;
}

.content-section ul {
  padding-left: 200px;
}

.submit-button {
  background-color: #37474f;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 15px 0;
  font-size: 16px;
}

.bounty-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.bounty-table th, .bounty-table td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: left;
}
